:root {
  --bg: rgb(18 18 18);
  --bg-accent: #484a4d;
  --text-color: #000;
  --nav-size: 60px;
  --border: 1px solid #474a4d;
  --border-radius: 8px;
  --speed: 500ms;
  --primary-color: #4e61ff
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

a {
  color: var(--text-color);
  text-decoration: none;
  ;
}

.navbar {
  z-index: 100;
  position: sticky;
  width: 100%;
  top: 0px;
  transform: translate3d(0px, 0px, 0px);
  -webkit-box-align: stretch;
  align-items: stretch;
  border-width: 0px;
  border-style: solid;
  border-color: rgb(255, 255, 255);
  display: flex;
  flex-basis: auto;
  flex-direction: column;
  flex-shrink: 0;
  min-height: 0px;
  min-width: 0px;
  max-width: 100%;
}

.navbar-container {
  background: rgb(18, 18, 18);
  color: rgb(255, 255, 255);
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  margin: 0px;
  padding: 0px 16px;
  border-width: 0px;
  border-style: solid;
  border-color: rgb(255, 255, 255);
  display: flex;
  flex-basis: auto;
  flex-shrink: 0;
  min-height: 0px;
  min-width: 0px;
  max-width: 100%;
  z-index: 0;
  flex-direction: row;
  -webkit-box-align: center;
  align-items: center;
  height: 84px;
  -webkit-box-pack: justify;
  justify-content: space-between;
}

.logo-container {
  width: 40px;
  height: 40px;
  transition: all 0.12s ease-in-out 0s;
  transform-origin: center center;
  text-decoration: none;
  color: rgb(45, 129, 255);
  margin-right: 24px;
}

.logo {
  width: 40px;
  height: 40px;
}

.nav-row{
  display: flex;
  flex: 0 0 auto;
  min-height: 0px;
  min-width: 0px;
  max-width: 100%;
  z-index: 0;
  flex-direction: row;
  -webkit-box-align: center;
  align-items: center;
  -webkit-box-flex: 0;
  -webkit-box-pack: center;
  justify-content: center;
}
.nav-row .divider-vertical{
  height: 24px;
  width: 1px;
  background: rgba(255, 255, 255, 0.1);
  margin: 0px 18px;
}

.sub-navbar {
  margin: 0px;
  padding: 0px;
  -webkit-box-align: stretch;
  align-items: stretch;
  border-width: 0px;
  border-style: solid;
  border-color: rgb(255, 255, 255);
  display: flex;
  flex-basis: auto;
  flex-shrink: 0;
  min-height: 0px;
  min-width: 0px;
  max-width: 100%;
  z-index: 0;
  flex-direction: row;
}

.sub-navbar .nav-item {
  margin: 0px 20px 0px 0px;
  padding: 0px;
  -webkit-box-align: stretch;
  align-items: stretch;
  border-width: 0px;
  border-style: solid;
  border-color: rgb(255, 255, 255);
  display: flex;
  flex-basis: auto;
  flex-direction: column;
  flex-shrink: 0;
  min-height: 0px;
  min-width: 0px;
  max-width: 100%;
  z-index: 0;
}

.sub-navbar .nav-item .item {
  margin: 0px;
  padding: 0px;
  border-width: 0px;
  border-style: solid;
  border-color: rgb(255, 255, 255);
  display: flex;
  flex-basis: auto;
  flex-shrink: 0;
  min-height: 0px;
  min-width: 0px;
  max-width: 100%;
  z-index: 0;
  -webkit-box-align: center;
  align-items: center;
  flex-direction: row;
}

.sub-navbar .nav-item .item a {
  transition: all 0.12s ease-in-out 0s;
  color: rgba(255, 255, 255, 0.5);
  height: 40px;
  display: flex;
  -webkit-box-align: center;
  align-items: center;
  flex-direction: row;
  cursor: pointer;
}


.sub-navbar .nav-item .item a:hover {
  color: rgba(255, 255, 255, 0.9);
}

.sub-navbar .nav-item .item .active {
  color: rgba(255, 255, 255, 0.9);
}


.connect-button{
  border: 1px solid transparent;
  border-color: rgba(255, 255, 255, 0.1);
  color: rgb(255, 255, 255);
  background: transparent;
  display: flex;
  flex-flow: row nowrap;
  height: 40px;
  padding-left: 22px;
  padding-right: 22px;
  min-width: auto;
  -webkit-box-align: center;
  align-items: center;
  -webkit-box-pack: center;
  justify-content: center;
  border-radius: 40px;
  font-size: 14px;
  font-weight: 900;
  transition: all 0.12s ease-in-out 0s;
  transform-origin: center center;
  cursor: pointer;
}
.connect-button:hover{
  border-color: rgba(255, 255, 255, 0.18);
}
.connect-button:active{
  transform: scale(0.95);}



.user-container {
  display: inline-block;
  position: relative;
}

