@media screen and (min-width: 981px) {
  .navbar-container {
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 768px) {
  .navbar-container {
    padding-left: 28px;
    padding-right: 28px;
  }
}

@media screen and (min-width: 578px) {
  .navbar-container {
    padding-left: 24px;
    padding-right: 24px;
  }
}