.footer {
    margin-top: 10%;
    background: rgb(18, 18, 18);
    border-top: 1px solid rgba(255, 255, 255, 0.1);
    color: rgba(255, 255, 255, 0.6);
}

.footer-container {
    padding: 64px 0px 0px;
    -webkit-box-align: stretch;
    align-items: stretch;
    border-width: 0px;
    border-style: solid;
    border-color: rgb(255, 255, 255);
    display: flex;
    flex-basis: auto;
    flex-direction: column;
    flex-shrink: 0;
    min-height: 0px;
    min-width: 0px;
    max-width: 100%;
    z-index: 0;
    width: 100%;
    margin: 0px auto;
    max-width: 1500px;
}

.footer-top {
    margin: 0px;
    padding: 0px 0px 32px;
    -webkit-box-align: stretch;
    align-items: stretch;
    border-width: 0px;
    border-style: solid;
    border-color: rgb(255, 255, 255);
    display: flex;
    flex-basis: auto;
    flex-shrink: 0;
    min-height: 0px;
    min-width: 0px;
    max-width: 100%;
    z-index: 0;
    flex-flow: row wrap;
    -webkit-box-pack: justify;
    justify-content: space-between;
}

.update-form-container {
    margin: 0px 0px 32px;
    padding: 0px;
    -webkit-box-align: stretch;
    align-items: stretch;
    border-width: 0px;
    border-style: solid;
    border-color: rgb(255, 255, 255);
    display: flex;
    flex-direction: column;
}

.update-form-container2 {
    -webkit-box-align: stretch;
    align-items: stretch;
    border-width: 0px;
    border-style: solid;
    border-color: rgb(255, 255, 255);
    display: flex;
    flex-basis: auto;
    flex-direction: column;
}

.update-form-container2 .title {
    font-weight: 900;
    color: rgb(255, 255, 255);
    line-height: 1.4;
    line-height: 1.4;
}

.update-form-container form {
    margin: 16px 0px 0px;
    padding: 0px;
    -webkit-box-align: stretch;
    align-items: stretch;
    border-width: 0px;
    border-style: solid;
    border-color: rgb(255, 255, 255);
    display: flex;
    flex-basis: auto;
    flex-direction: column;
    flex-shrink: 0;
    min-height: 0px;
    min-width: 0px;
    max-width: 100%;
    z-index: 0;
    position: relative;
}

.update-form-container form .form-input {
    background: rgb(31, 31, 31);
    height: 48px;
    border-radius: 48px;
    border: 1px solid rgba(255, 255, 255, 0.1);
    transition: all 0.12s ease-in-out 0s;
    -webkit-box-align: stretch;
    align-items: stretch;
    padding: 0px 95px 0px 20px;
    flex-direction: row;
}

.update-form-container form .form-input input {
    color: rgb(255, 255, 255);
    -webkit-box-flex: 1;
    flex-grow: 1;
    font-weight: 500;
    font-size: 15px;
    background-color: rgba(0, 0, 0, 0);
    height: 100%;
    outline: none;
    border: none;
}

.update-form-container form .form-button {
    margin: 0px;
    padding: 0px;
    -webkit-box-align: stretch;
    align-items: stretch;
    border-width: 0px;
    border-style: solid;
    border-color: rgb(255, 255, 255);
    display: flex;
    flex-basis: auto;
    flex-direction: column;
    flex-shrink: 0;
    min-height: 0px;
    min-width: 0px;
    max-width: 100%;
    z-index: 0;
    position: absolute;
    top: 0px;
    right: 0px;
}

.update-form-container form .form-button button {
    overflow: hidden;
    height: 48px;
    border: 0px;
    color: rgb(255, 255, 255);
    background: rgb(45, 129, 255);
    display: flex;
    flex-flow: row nowrap;
    height: 48px;
    padding-left: 26.4px;
    padding-right: 26.4px;
    border: 1px solid transparent;
    -webkit-box-align: center;
    align-items: center;
    -webkit-box-pack: center;
    justify-content: center;
    border-radius: 48px;
    font-size: 15px;
    font-weight: 900;
    font-family: inherit;
    transition: all 0.12s ease-in-out 0s;
    transform-origin: center center;
    user-select: none;
    cursor: pointer;
    width: auto;
}

.link-list {}

.link-list .title {
    text-decoration: none;
    font-size: 18px;
    line-height: 24.84px;
    font-family: inherit;
    font-weight: 900;
    color: rgb(255, 255, 255);
}

.link-list ul {
    margin: 16px 0px 0px;
    padding: 0px;
    -webkit-box-align: stretch;
    align-items: stretch;
    border-width: 0px;
    border-style: solid;
    border-color: rgb(255, 255, 255);
    display: flex;
    flex-basis: auto;
    flex-direction: column;
    flex-shrink: 0;
    min-height: 0px;
    min-width: 0px;
    max-width: 100%;
    z-index: 0;
}

.link-list ul li {
    margin: 0px 0px 12px;
    padding: 0px;
    -webkit-box-align: stretch;
    align-items: stretch;
    border-width: 0px;
    border-style: solid;
    border-color: rgb(255, 255, 255);
    display: flex;
    flex-basis: auto;
    flex-direction: column;
    flex-shrink: 0;
    min-height: 0px;
    min-width: 0px;
    max-width: 100%;
    z-index: 0;
}

.link-list ul li a {
    transition: all 0.12s ease-in-out 0s;
    text-decoration: none;
    color: rgb(45, 129, 255);
    color: rgba(255, 255, 255, 0.6);
}

.link-list ul li a:hover {
    color: rgba(255, 255, 255, 1);
}

.dark-theme .link-list ul li a span {
    font-size: 16px;
    line-height: 22.08px;
    font-family: inherit;
    font-weight: 900;
}

.footer-bottom {
    margin: 0px;
    padding: 32px 0px;
    border-width: 0px;
    border-style: solid;
    border-color: rgb(255, 255, 255);
    display: flex;
    flex-basis: auto;
    flex-shrink: 0;
    min-height: 0px;
    min-width: 0px;
    max-width: 100%;
    z-index: 0;
    flex-direction: column;
    -webkit-box-align: center;
    align-items: center;
    -webkit-box-pack: justify;
    justify-content: space-between;
    border-top: 1px solid rgba(255, 255, 255, 0.1);
}

.footer-bottom .copyright {}

.footer-bottom .copyright span {
    font-size: 14px;
}

.footer-bottom .link a {
    transition: all 0.12s ease-in-out 0s;
    color: rgba(255, 255, 255, 0.6);
}

.footer-bottom .link a:hover {
    color: rgba(255, 255, 255, 0.9);
}

@media screen and (min-width: 981px) {
    .footer-top {
        flex-wrap: nowrap;
    }
    .footer-bottom {
        flex-direction: row;
        -webkit-box-pack: center;
        justify-content: center;
    }
}

@media screen and (min-width: 768px) {
    .footer-top {
        flex-wrap: wrap;
        padding-bottom: 64px;
    }
    .footer-bottom {
        flex-direction: column;
        -webkit-box-pack: justify;
        justify-content: space-between;
    }
}

@media screen and (min-width: 578px) {
    .footer-top {
        flex-wrap: wrap;
        padding-bottom: 32px;
    }
    .footer-bottom {
        flex-direction: column;
        -webkit-box-pack: justify;
        justify-content: space-between;
    }
}